.prizes-section {
  .container {
    @include vp-1023 {
      padding: 0;
    }
  }

  &__title {
    margin: 0 0 35px;
    line-height: 100%;

    @include vp-1023 {
      padding: 0 clamp(32px, $padding-tablet, 115px);
    }

    @include vp-767 {
      padding: 0 clamp(20px, $padding-mobile, 32px);
      line-height: 120%;
    }
  }

  &__wrapper {
    display: flex;
    flex-wrap: wrap;
    gap: 37px;
    margin-bottom: 40px;

    @include vp-1023 {
      gap: 32px;
    }

    @include vp-767 {
      margin-bottom: 20px;
    }
  }

  &--main-weekly .prizes-section__title {
    margin-bottom: 34px;

    @include vp-767 {
      margin-bottom: 20px;
      padding-top: 2px;
    }
  }

  &--main-weekly .prizes-section__wrapper {
    margin-bottom: 50px;

    @include vp-1439 {
      row-gap: 30px;
      column-gap: 30px;
    }

    @include vp-1023 {
      margin-bottom: 0;
      gap: 0;
    }
  }

  &--main-weekly .prize-category {
    @include vp-1023 {
      padding: 12px clamp(32px, $padding-tablet, 115px) 21px;
    }

    @include vp-767 {
      padding: 12px clamp(20px, $padding-mobile, 32px) 21px;
    }
  }

  &--main-weekly .category-prize-list {
    @include vp-1023 {
      gap: 30px;
    }

    @include vp-767 {
      gap: 0;
    }
  }

  &--main-weekly .prize-category__title {
    letter-spacing: -0.8px;

    @include vp-1023 {
      margin-bottom: 15px;
    }
  }

  &--bonuses {
    .prize-category__title {
      max-width: initial;
      margin-bottom: 8px;
      letter-spacing: -0.6px;

      @include vp-1023 {
        padding-right: clamp(32px, $padding-tablet, 115px);
      }

      @include vp-767 {
        padding-right: clamp(20px, $padding-mobile, 32px);
      }
    }

    .category-prize-list {
      row-gap: 16px;
      column-gap: 17px;

      @include vp-1023 {
        gap: 0;
      }
    }

    .prize-category {
      padding: 32px 32px 40px;
      background: $color-default-white;

      @include vp-1023 {
        padding: 0 0 0 clamp(32px, $padding-tablet, 115px);
      }

      @include vp-767 {
        padding: 0 0 0 clamp(20px, $padding-mobile, 32px);
      }
    }

    .prize-category .container {
      padding-right: 0;
    }

    .card-prize {
      min-width: 160px;

      @include vp-767 {
        min-width: unset;
        width: 156px;
        max-width: 156px;
      }
    }

    .card-prize--wide {
      min-width: 336px;

      @include vp-767 {
        min-width: initial;
      }
    }

    .card-prize--wide .card-prize__text {
      width: 141px;
    }

    .card-prize__title {
      font-size: 18px;
      margin-bottom: 2px;
    }

    .card-prize__text {
      font-size: 14px;
    }

    .card-prize--wide picture img {
      margin-right: 14px;

      @include vp-1023 {
        right: 0;
      }
    }
  }

  &--partners-offers,
  &--beginner,
  &--stay-withus,
  &--more-prizes {
    position: relative;

    .container {
      @include vp-1023 {
        padding: 0;
      }
    }

    .prize-category {
      width: 100%;
      padding-bottom: 36px;

      @include vp-1023 {
        background: none;
        padding: 0 0 0 clamp(32px, $padding-tablet, 115px);
      }

      @include vp-767 {
        padding: 0 0 0 clamp(24px, $padding-mobile, 32px);
      }
    }

    .category-prize-list {
      gap: 16px;

      @include vp-1023 {
        gap: 0;
      }
    }

    .prize-category__title {
      max-width: initial;
    }
  }

  &--stay-withus {
    .card-prize {
      width: 230px;
      max-width: unset;

      @include vp-767 {
        width: 156px;
        max-width: 156px;
      }
    }

    .card-prize__title {
      font-size: 24px;

      @include vp-1023 {
        font-size: 16px;
      }
    }
  }

  &--more-prizes {
    .card-prize {
      width: 230px;
      max-width: unset;

      @include vp-767 {
        max-width: 156px;
        width: 156px;
      }
    }

    .card-prize__title {
      font-size: 24px;

      @include vp-1023 {
        font-size: 16px;
      }
    }
  }

  &--partners-offers {
    .card-prize {
      width: 160px;
      max-width: unset;

      @include vp-767 {
        width: 156px;
        max-width: 156px;
      }
    }

    .card-prize__title {
      font-size: 16px;
    }

    .card-prize__text {
      font-size: 14px;
      font-weight: 500;
    }
  }

  &--main-monthly {
    .container {
      @include vp-1023 {
        padding: 0;
      }
    }

    .prizes-section__wrapper {
      margin-bottom: 48px;

      @include vp-767 {
        gap: 0;
        margin-bottom: 10px;
      }
    }

    .prize-category {
      @include vp-1023 {
        padding: 20px clamp(32px, $padding-tablet, 115px);
      }

      @include vp-767 {
        padding: 14px clamp(20px, $padding-mobile, 32px) 20px;
      }
    }

    .prize-category__title {
      @include vp-767 {
        margin-bottom: 15px;
      }
    }

    .prizes-section__title {
      @include vp-1023 {
        padding: 20px clamp(32px, $padding-tablet, 115px);
      }

      @include vp-767 {
        padding: 11px clamp(20px, $padding-mobile, 11px);
        margin-bottom: 7px;
      }
    }

    .category-prize-list {
      @include vp-1023 {
        gap: 30px;
      }

      @include vp-767 {
        gap: 0;
      }
    }
  }

  &--beginner-weekly {
    .container {
      @include vp-1023 {
        padding: 0;
      }
    }

    .prize-category {
      width: 100%;
      padding-bottom: 36px;

      @include vp-1023 {
        padding: 20px clamp(32px, $padding-tablet, 115px);
        background: $color-default-white;
      }

      @include vp-767 {
        padding: 14px clamp(20px, $padding-mobile, 32px);
      }
    }

    .prize-category__wrapper {
      @include vp-1023 {
        margin-bottom: 20px;
      }
    }

    .prize-category__title {
      max-width: initial;
    }

    .prize-category__link {
      display: none;

      @include vp-1023 {
        display: flex;
        max-width: 325px;
        margin: 0 auto;
      }
    }

    .card-prize {
      width: 230px;
      max-width: unset;

      @include vp-1023 {
        width: 156px;
        max-width: 156px;
      }
    }

    .card-prize__title {
      font-size: 24px;
      font-weight: 700;

      @include vp-1023 {
        font-size: 16px;
      }
    }

    .btn--primary {
      color: $color-default-white;

      .btn__progress-bar {
        background: linear-gradient(180deg, rgba(122, 231, 255, 1) 0%, rgba(23, 199, 255, 1) 100%);
        width: 90%;
      }
    }

    .btn--primary:active {
      .btn__progress-bar {
        width: 100%;
      }
    }
  }
}
