.footer {
  margin-bottom: 164px;
  font-weight: 500;
  font-size: 14px;
  line-height: 18px;

  @include vp-1023 {
    margin-bottom: 28px;
  }

  &__container {
    max-width: 1440px;
    padding: 32px 32px 40px;
    background-color: $color-default-white;
    border-top: 1px solid $color-solitude-second;
    border-radius: 40px;
    color: $color-fiord;

    @include vp-1023 {
      padding: 0;
      background-color: transparent;
      border: none;
    }
  }

  &__image-box {
    margin-bottom: 28px;

    @include vp-1023 {
      display: none;
    }
  }

  &__wrapper {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 32px;

    @include vp-1023 {
      grid-template-columns: 1fr;
    }
  }

  &__copyright {
    margin-top: auto;

    span {
      display: block;
    }

    @include vp-1023 {
      display: none;
    }
  }

  &__list {
    margin: auto 0 0;
    padding: 0;
    display: grid;
    grid-template-columns: 1fr;
    row-gap: 11px;
    list-style-type: none;

    @include vp-1023 {
      display: flex;
      flex-wrap: wrap;
      row-gap: 5px;
      margin-right: -2px;
    }
  }

  &__item {
    @include vp-1023 {
      margin-right: 12px;

      &--policy {
        order: 2;
      }

      &--agreement {
        order: 3;
      }

      &--rules {
        order: 1;
      }
    }
  }

  &__link {
    color: $color-fiord;
    font-size: 16px;
    line-height: 22px;
    text-decoration-line: underline;
    transition: color $trans-default, border-bottom-color $trans-default;
    user-select: none;

    &:hover {
      color: $color-nobel;
    }

    &:active {
      color: $color-nero;
    }

    @include vp-1023 {
      text-decoration-line: none;
      font-size: 14px;
      line-height: 15px;
      letter-spacing: -0.33px;
    }
  }
}
