.prizes-window {
  margin: 0;
  padding: 20px 0;

  @include vp-1023 {
    padding: 100px 0;
  }

  @include vp-767 {
    padding: 17px 0 15px;
  }

  &--second {
    @include vp-767 {
      padding-bottom: 3px;
    }
  }

  &__list {
    margin: 0;
    padding: 32px 32px 20px 32px;
    border-radius: 40px;
    background-color: $color-default-white;
    width: 100%;

    @include vp-1023 {
      padding: 0;
      border-radius: unset;
      margin: 0 auto;
    }
  }

  &__title {
    width: 100%;
    margin-bottom: 20px;

    h2 {
      margin: 0;
      padding: 0;
      font-weight: 700;
      font-size: 32px;
      line-height: 38px;
      letter-spacing: -0.4px;

      @include vp-1023 {
        font-size: 28px;
        line-height: 28px;
      }

      @include vp-767 {
        font-size: 24px;
        line-height: 28px;
      }
    }
  }

  &__list-wrapper {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    gap: 20px 16px;

    @include vp-1023 {
      flex-wrap: nowrap;
      gap: 0;
    }
  }

  @media (min-width: 1024px) {
    .swiper-slide {
      flex-shrink: 0;
      width: unset;
      height: unset;
      position: unset;
      transition-property: unset;
    }
  }

  &__list-item {
    @include vp-1023 {
      margin: 0;
      padding: 0;
      width: fit-content;
    }
  }

  &__item-prize {
    width: 16vw;
    max-width: 230px;

    @include vp-1023 {
      width: unset;
      max-width: unset;
    }

    .card-prize__logo-wrapper {
      justify-content: unset;

      img {
        margin-right: 11px;
      }
    }

    .card-prize__title {
      font-size: 24px;
      max-width: 200px;
      position: relative;
      z-index: 2;
      margin-bottom: 0;

      @include vp-1279 {
        font-size: 22px;
      }

      @include vp-1023 {
        font-size: 20px;
        line-height: 20px;
        max-width: 300px;
      }

      @include vp-767 {
        font-size: 16px;
        line-height: 20px;
        max-width: 133px;
      }
    }

    span {
      position: relative;
      z-index: 2;
    }
  }

  &__link {
    @media (min-width: 1024px) {
      display: none;
    }

    width: 100%;
    margin: 0 auto;
    margin-top: 20px;
    line-height: 20px;
    letter-spacing: -0.4px;

    @include vp-767 {
      font-size: 18px;
    }
  }
}

.prizes-window--partners-offers {
  .prizes-window__title {
    margin-bottom: 24px;
  }

  .prizes-window__item-prize {
    max-width: 160px;

    @media (max-width: 1023px) {
      width: unset;
      max-width: unset;
    }
  }


  .prizes-window__item-prize .card-prize__title {
    font-size: 18px;
    max-width: 141px;
  }

  .card-prize {
    padding: 12px 12px 0;
  }

  .card-prize__text {
    font-size: 14px;
    line-height: 18px;
    font-weight: 500;
  }

  .prizes-window__item-prize .card-prize__logo-wrapper img {
    margin-right: 0;
  }

  .card-prize__logo-wrapper {
    margin-bottom: 3px;
  }
}
