.agreement {
  display: flex;
  flex-direction: column;
  align-items: center;

  .checkbox {
    margin-bottom: 16px;


    @include vp-1023 {
      margin-bottom: 15px;
    }
  }

  .custom-toggle {
    width: unset;
    padding: 0;

    &:last-of-type {
      padding: 0;

      @media screen and (max-width: 767px) {
        padding: 0;
      }
    }
  }

  &__button {
    width: 327px;

    @include vp-767 {
      width: 100%;
      margin-bottom: 0;
    }
  }
}
