.header {
  padding: 63px 0 30px;
  position: absolute;
  background-color: transparent;
  z-index: 10;
  width: 100%;
  display: flex;
  justify-content: flex-start;

  @include vp-1023 {
    padding: 30px 0 30px;
  }

  @include vp-767 {
    padding: 19px 0 19px;
  }

  &--center {
    padding: 42px 0 47px;

    @include vp-1023 {
      padding: 30px 0 30px;
    }

    @include vp-767 {
      padding: 19px 0 19px;
    }
  }

  .container {
    display: flex;

    @include vp-1023 {
      justify-content: center;
    }
  }

  &--center .container {
    justify-content: center;
  }

  &__logo-wrapper img {
    width: 167px;
    height: 28px;

    @include vp-1023 {
      width: 150px;
      height: 20px;
    }

    @include vp-767 {
      width: 119px;
      height: 21px;
    }
  }

  &__logo-link {
    cursor: pointer;

    @include hover-focus {
      opacity: 0.8;
    }
  }
}
