.winners-list {
  padding: 0 0 50px 0;

  @include vp-1023 {
    padding: 9px 0 39px;
  }

  @include vp-1023 {
    &__overlay {
      width: 0;
      height: 0;
      opacity: 0;
      transition: transform 0.3s ease, opacity 0.3s ease;
    }

    &__overlay--hidden {
      width: 0;
      height: 0;
    }

    &__overlay--shown {
      background-color: $color-default-black;
      opacity: 20%;
      position: absolute;
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
      z-index: 9;
      transition: transform 0.3s ease, opacity 0.3s ease;
    }
  }

  h2 {
    padding: 0;
    margin: 0;
    margin-bottom: 35px;
    font-weight: 700;
    font-size: 48px;
    line-height: 48px;
    color: $color-nero;
    letter-spacing: -0.02em;
    text-align: left;

    @include vp-1023 {
      font-size: 40px;
      line-height: 42.4px;
      margin-bottom: 30px;
      text-align: center;
    }

    @include vp-767 {
      font-size: 32px;
      line-height: 38.4px;
      margin-bottom: 30px;
    }
  }

  &__buttons-wrapper {
    margin: 0 auto;
    margin-bottom: 50px;
    padding: 0;
    width: 100%;
    max-width: 1110px;
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 30px;

    @include vp-1023 {
      display: flex;
      flex-direction: column;
      gap: unset;
      margin-bottom: 20px;
    }

    @include vp-767 {
      margin-bottom: 15px;
    }
  }

  &__button {
    padding: 32px 10px;
    width: 100%;
    max-width: 540px;
    background-color: $color-default-white;
    color: $color-cornflower-blue;
    font-size: 32px;
    line-height: 38px;
    border-radius: 20px;
    letter-spacing: -0.02em;
    font-weight: 700;
    position: relative;

    @media (min-width: 1024px) {
      z-index: 100;
    }

    @include hover-focus {
      opacity: 1;
    }

    @include vp-1023 {
      padding: 20px 5px;
      font-size: 24px;
      line-height: 24px;
      margin-bottom: 20px;
      background-color: $color-alice-blue-third;
      border-radius: 15px;
      max-width: unset;

      @include hover-focus {
        opacity: 1;
        background: transparent;
        border: 1px solid #498ae9;
        padding: 19px 4px;

        @include active {
          opacity: 1;
          background: transparent;
          border: 1px solid #498ae9;
          padding: 19px 4px;
        }
      }
    }

    @include vp-767 {
      padding: 15px 5px;
      font-size: 18px;
      line-height: 19.8px;
      margin-bottom: 15px;
      background-color: $color-alice-blue-third;
      border-radius: 12px;
      max-width: unset;

      @include hover-focus {
        opacity: 1;
        background: transparent;
        border: 1px solid #498ae9;
        padding: 14px 4px;

        @include active {
          opacity: 1;
          background: transparent;
          border: 1px solid #498ae9;
          padding: 14px 4px;
        }
      }
    }
  }

  &__button span {
    @media (min-width: 1024px) {
      opacity: 0;
      border-radius: 20px;
      position: absolute;
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
      z-index: -1000;
      background: linear-gradient(143.22deg, #c4e3ff 3.18%, #ffffff 46.8%);
      transition: opacity $trans-default;
    }
  }

  &__button:hover span {
    @media (min-width: 1024px) {
      opacity: 1;
    }
  }

  &__select-wrapper {
    display: flex;
    width: 100%;
    max-width: 1110px;
    margin: 0;
    padding: 0;
    margin-bottom: 50px;

    @include vp-1023 {
      max-width: 1023px;
      margin-bottom: 40px;
    }

    @include vp-767 {
      max-width: 767px;
      margin-bottom: 32px;
    }

    .custom-select {
      margin: 0 auto;
      padding: 0;
      width: initial;

      @include vp-1023 {
        max-width: unset;
        width: 100%;
        position: unset;
      }
    }

    .custom-select__button {
      background-color: $color-cornflower-blue;
      padding: 30px;
      margin: 0 auto;
      height: unset;
      border: none;
      border-radius: 20px;
      color: $color-default-white;
      font-weight: 700;
      font-size: 24px;
      line-height: 28px;
      justify-content: center;

      @include hover-focus {
        opacity: 0.8;
        transition: opacity $trans-default;
      }

      &:active {
        opacity: 0.6;
      }

      @include vp-1023 {
        max-width: unset;
        border-radius: 15px;
        padding: 18px;
      }

      @include vp-767 {
        padding: 15px 17px;
        border-radius: 12px;
      }
    }

    .custom-select__text {
      width: fit-content;
      font-size: 24px;
      font-weight: 700;
      line-height: 28.8px;
      letter-spacing: -0.02em;
      overflow: unset;
      white-space: unset;
      text-align: center;

      @include vp-767 {
        font-size: 16px;
        line-height: 20.16px;
      }
    }

    .custom-select__icon {
      margin: 0;
      margin-left: 10px;
      background-color: transparent;
      width: unset;
      height: unset;
      clip-path: none;
      display: flex;
    }

    .custom-select__list {
      bottom: -20px;
      max-height: fit-content;
      border: none;
      background-color: $color-alice-blue-third;
      border-radius: 20px;
      padding: 30px 10px 30px 10px;

      @include vp-1023 {
        background-color: $color-default-white;
        padding: 20px;
        max-width: 500px;
        top: 50%;
        position: fixed;
        left: 50%;
        z-index: 10;
        transform: translate(-50%, -50%);
      }

      @include vp-767 {
        max-width: 309px;
        padding: 20px;
        background-color: $color-default-white;
        top: 50%;
        position: fixed;
        left: 50%;
        z-index: 10;
        transform: translate(-50%, -50%);
      }
    }

    .custom-select__item[aria-selected="true"] {
      background-color: unset;
    }

    .custom-select__item {
      padding: 10px;
      color: $color-dodger-blue;
      font-weight: 700;
      font-size: 24px;
      line-height: 29px;
      letter-spacing: -0.02em;
      border-radius: 20px;
      text-align: left;

      @include vp-1023 {
        font-size: 20px;
        line-height: 20px;
        text-align: left;
      }

      @include vp-767 {
        font-size: 16px;
        line-height: 20.16px;
        text-align: left;
      }

      @include hover-focus {
        color: $color-default-white;
        background-color: $color-dodger-blue;
      }
    }
  }

  &__table {
    background-color: $color-default-white;
    border-radius: 40px;
    padding: 32px;
    padding-bottom: 18px;

    @include vp-767 {
      background-color: transparent;
      border-radius: unset;
      padding: 0;
    }

    &--full {
      h3 {
        display: none;
      }
    }

    &--empty {
      transition: opacity $trans-default;

      .winners-list__table-title,
      .winners-list__info,
      .winners-list__winners {
        display: none;
      }

      h3 {
        font-size: 40px;
        line-height: 40px;
        letter-spacing: -0.02em;
        text-align: center;

        @include vp-1023 {
          font-size: 32px;
          line-height: 32px;
        }

        @include vp-767 {
          font-size: 24px;
          line-height: 29px;
        }
      }
    }
  }

  &__table-title {
    display: flex;
    justify-content: space-between;
    margin-bottom: 30px;
    align-items: center;

    p {
      margin: 0;
      padding: 0;
      font-size: 32px;
      font-weight: 700;
      line-height: 38.4px;
      letter-spacing: -0.02em;
      color: $color-nobel;
      max-width: 500px;
    }

    .winners-list__title-week {
      @include vp-767 {
        font-size: 24px;
        line-height: 28.8px;
        letter-spacing: -0.02em;
        max-width: 130px;
      }
    }

    .winners-list__title-date {
      @include vp-767 {
        font-size: 18px;
        font-weight: 700;
        line-height: 19.8px;
        letter-spacing: -0.02em;
        max-width: 200px;
      }
    }
  }

  &__info {
    list-style-type: none;
    margin: 0 auto;
    margin-bottom: 30px;
    padding: 0;
    display: flex;
    max-width: 1110px;
    position: relative;

    &::after {
      content: "";
      width: 100%;
      height: 1px;
      background-color: $color-nobel;
      position: absolute;
      bottom: -15px;
      display: block;
    }
  }

  &__info-item {
    margin: 0;
    padding: 0;
    margin-right: 30px;
    font-size: 24px;
    font-weight: 700;
    line-height: 28.8px;
    letter-spacing: -0.02em;

    @include vp-767 {
      font-size: 18px;
      line-height: 19.8px;
      margin-right: 10px;
    }

    p {
      margin: 0;
      padding: 0;
    }

    &--name {
      width: 100%;
      max-width: 150px;

      @include vp-767 {
        max-width: 24vw;
      }
    }

    &--phone {
      width: 100%;
      max-width: 180px;

      @include vp-767 {
        max-width: 29.3vw;
        margin-right: 25px;
      }
    }

    &--prize {
      width: 100%;
      max-width: 600px;

      @include vp-767 {
        max-width: 28vw;
      }
    }
  }

  &__winners {
    list-style-type: none;
    padding: 0;
    margin: 0;
    max-height: 610px;
    overflow-y: scroll;

    @include vp-767 {
      max-height: 625px;
    }
  }

  &__winners-item {
    padding: 0;
    margin: 0;
    margin-bottom: 15px;
    display: flex;
    flex-wrap: nowrap;

    @include vp-1023 {
      margin-bottom: 16px;
    }

    p {
      padding: 0;
      font-size: 24px;
      font-weight: 500;
      line-height: 32px;
      letter-spacing: -0.01em;
      text-align: left;

      @include vp-767 {
        font-size: 16px;
        font-weight: 500;
        line-height: 21.6px;
        letter-spacing: -0.02em;
        text-align: left;
      }

      @media (max-width: 374px) {
        font-size: 14px;
      }
    }
  }

  &__winners-text {
    width: 100%;
    margin: 0;
    margin-right: 30px;

    @include vp-767 {
      margin-right: 10px;
    }
  }

  &__winners-text--name {
    max-width: 150px;

    @include vp-767 {
      max-width: 24vw;
    }
  }

  &__winners-text--phone {
    max-width: 180px;
    margin-right: 30px;

    @include vp-767 {
      max-width: 29.3vw;
      margin-right: 19px;
    }

    @media (max-width: 374px) {
      margin-right: 10px;
    }
  }

  &__winners-text--prize {
    max-width: 650px;

    @include vp-767 {
      max-width: 28vw;
    }
  }
}
