.prize-category {
  padding: 32px 32px 32px;
  background-color: $color-default-white;
  border-radius: 40px;

  @include vp-1023 {
    background: $color-solitude-to-down;
    width: 100%;
    padding: 20px 0;
  }

  @include vp-767 {
    padding: 0;
  }

  @include vp-767 {
    border-radius: 20px;
  }

  &__title {
    max-width: 159px;
    margin: 0 0 23px;
    line-height: 120%;

    @include vp-1023 {
      max-width: unset;
    }

    &--less {
      color: #48abd3;
    }

    &--between {
      color: #4891d3;
    }

    &--more {
      color: #498ae9;
    }
  }

  &__text {
    margin: 0 0 24px;
    letter-spacing: -0.2px;

    @include vp-1023 {
      padding-right: clamp(32px, $padding-tablet, 115px);
    }

    @include vp-767 {
      padding-right: clamp(20px, $padding-mobile, 32px);
    }
  }
}
