.main-hero {
  padding: 142px 0 41px;
  min-height: 655px;
  max-width: 1440px;
  margin: 0 auto;
  position: relative;

  @include vp-1023 {
    padding: 100px 0 50px;
    max-width: 1023px;
    min-height: 752px;
  }

  @include vp-767 {
    padding: 79px 0 30px;
    max-width: 767px;
    min-height: 752px;
  }

  @media (max-width: 374px) {
    min-height: auto;
  }

  .container {
    @include vp-1023 {
      padding: 0;
    }
  }

  &__wrapper {
    width: 100%;
    position: relative;
    z-index: 5;
    display: flex;

    @include vp-1023 {
      flex-direction: column;
    }
  }

  &__title-wrapper {
    max-width: 440px;

    @include vp-1439 {
      max-width: 31vw;
    }

    @include vp-1023 {
      max-width: 100%;
      margin: 0 auto;
      padding: 0 clamp(20px, 6.4%, 32px);
    }

    @include vp-767 {
      padding: 0 clamp(20px, 6.4%, 32px);
    }
  }

  &__title-wrapper h2 {
    margin: 0;
    padding: 0;
    color: $color-dodger-blue;
    font-size: 64px;
    line-height: 70px;
    letter-spacing: -1px;
    margin-bottom: 40px;
    max-width: 440px;

    @include vp-1439 {
      font-size: 4vw;
      line-height: 4vw;
      max-width: 31vw;
    }

    @include vp-1279 {
      font-size: 3.5vw;
      line-height: 3.5vw;
    }

    @include vp-1023 {
      margin: 0;
      color: $color-default-white;
      max-width: 100%;
      font-size: 12.8vw;
      line-height: 12.8vw;
      letter-spacing: -1px;
    }
  }

  &__content-wrapper {
    @media (min-width: 1024px) {
      display: flex;
      flex-direction: column;

      .main-hero__title-wrapper {
        order: 1;
      }

      .main-hero__text-wrapper {
        order: 2;
      }

      .main-hero__extra-block {
        order: 3;
      }
    }

    max-width: 440px;

    @include vp-1439 {
      max-width: 31vw;
    }

    @include vp-1023 {
      max-width: 100%;
      margin: 0 auto;
      z-index: 2;
    }
  }

  &__text-wrapper {
    max-width: 440px;

    @include vp-1439 {
      max-width: 31vw;
    }

    @include vp-1023 {
      max-width: 100%;
      padding: 0 clamp(20px, 6.4%, 32px);
    }

    @include vp-767 {
      padding: 0 clamp(20px, 6.4%, 32px);
    }
  }

  &__text-wrapper p {
    margin: 0;
    padding: 0;
    max-width: 440px;
    color: $color-nero;
    font-weight: 500;
    font-size: 24px;
    line-height: 32px;
    margin-bottom: 40px;
    letter-spacing: -0.01em;
    text-align: left;

    @include vp-1439 {
      max-width: 31vw;
      font-size: 1.7vw;
      line-height: 2.2vw;
    }

    @include vp-1279 {
      font-size: 1.5vw;
      line-height: 2vw;
    }

    @include vp-1023 {
      margin-bottom: 24px;
      max-width: 100%;
      font-size: 4.8vw;
      line-height: 6.4vw;
      letter-spacing: -0.02em;
    }

    @media (max-width: 374px) {
      margin-top: 10px;
    }
  }

  &__pictures-wrapper {
    position: absolute;
    top: 0;
    z-index: 4;

    @include vp-1023 {
      width: 100%;
      height: 100%;
      min-height: 480px;
      z-index: 1;
      top: -100px;
    }
  }

  &__mask-wrapper {
    position: relative;
    z-index: 1;

    @include vp-1023 {
      width: 100%;
      height: 100%;

      img {
        width: 100%;
        height: 100%;
      }
    }
  }

  &__gifts-wrapper {
    width: fit-content;
    margin-top: -143px;
    right: -60px;
    position: relative;

    @include vp-1023 {
      margin: 0 auto;
      right: unset;
    }

    @include vp-767 {
      margin-top: -35px;
      right: 12px;
    }
  }

  &--index &__extra-block {
    max-width: 327px;

    .agreement {
      @include vp-767 {
        align-items: start;
      }
    }

    @include vp-1023 {
      max-width: none;
      width: 100%;
      padding: 0 clamp(20px, 6.4%, 32px);
      margin: 0 auto;
    }

    .checkbox__title {
      margin: 0;
      padding: 0;
    }

    .custom-toggle {
      margin: 0;
      padding: 0;
    }

    .agreement .checkbox {
      @include vp-1023 {
        margin-bottom: 13px;
      }
    }

    .custom-toggle__label {
      font-size: 18px;
    }
  }

  &--winners {
    padding-bottom: 0;

    @include vp-1439 {
      min-height: 0;
    }

    @include vp-1023 {
      min-height: 0;
      padding-bottom: 0;
    }

    @include vp-767 {
      padding-bottom: 0;
      min-height: 0;
    }

    .main-hero__content-wrapper {
      max-width: 540px;
      width: 100%;

      @include vp-1439 {
        max-width: 38vw;
        margin-right: -80px;
      }

      @include vp-1199 {
        max-width: 40vw;
        margin-right: -100px;
      }

      @include vp-1023 {
        max-width: 100%;
        margin: 0 auto;
        z-index: 2;
      }
    }

    .main-hero__title-wrapper h2 {
      @include vp-1199 {
        margin-bottom: 5px;
      }

      @include vp-1023 {
        margin-bottom: 0;
      }
    }

    .main-hero__gifts-wrapper {
      right: 0;
      margin-right: -70px;
      margin-left: -15px;
      width: 100%;
      max-width: 654px;

      @include vp-1439 {
        width: fit-content;
        max-width: none;
        margin-right: 0;
        margin-left: 0;
        right: -60px;
      }

      @include vp-1023 {
        margin: 0 auto;
        right: 12px;
      }

      @include vp-767 {
        margin-top: -35px;
      }
    }

    .main-hero__extra-block {
      background-color: $color-default-white;
      margin-top: 5px;
      margin-bottom: 80px;
      padding: 32px 32px 7px;
      border-radius: 40px;

      @include vp-1279 {
        margin-top: 20px;
        margin-bottom: 60px;
      }

      @include vp-1199 {
        margin-bottom: 40px;
        padding: 24px 24px 7px;
      }

      @include vp-1023 {
        max-width: none;
        width: 100%;
        margin-top: 0;
        margin-bottom: 0;
        padding: 0 clamp(20px, 6.4%, 32px);
      }

      @include vp-374 {
        padding-top: 10px;
      }
    }

    .main-hero__text-wrapper {
      display: none;

      @include vp-1279 {
        display: none;
      }

      @include vp-1023 {
        display: none;
      }

      @include vp-767 {
        display: none;
      }
    }
  }
}
