.search-form {
  &__subtitle {
    margin: 0 0 20px;
    font-size: 32px;
    line-height: 38px;
    letter-spacing: -0.6px;
    color: $color-nero;

    @include vp-1279 {
      font-size: 28px;
      line-height: 30px;
    }

    @include vp-1023 {
      margin: 0 0 15px;
      font-size: 24px;
      line-height: 29px;
      letter-spacing: -0.4px;
    }
  }

  &__wrapper {
    position: relative;
    padding-bottom: 25px;
    display: flex;
    flex-direction: column;
  }

  &__input {
    width: 100%;
    height: 80px;
    margin-bottom: 20px;
    padding: 21px 57px 21px 25px;
    border: none;
    border-radius: 20px;
    background-color: $color-alice-blue-third;
    font-weight: 700;
    font-size: 32px;
    line-height: 38px;
    letter-spacing: -0.5px;
    color: $color-dodger-blue;

    @include vp-1023 {
      height: 60px;
      margin-bottom: 13px;
      padding: 20px 45px 20px 20px;
      font-size: 18px;
      line-height: 20px;
    }
  }

  &__input:hover {
    opacity: 0.8;
  }

  &.is-not-outline {
    .search-form__input:focus {
      outline-color: transparent;
    }
  }

  &__label {
    text-align: center;
    font-weight: 500;
    line-height: 22px;
    letter-spacing: -0.3px;
    color: $color-nobel;

    @include vp-1023 {
      text-align: start;
    }
  }

  &__button {
    position: absolute;
    right: 24px;
    top: 25px;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 30px;
    height: 30px;
    padding: 0;
    border: none;
    background-color: transparent;
    cursor: pointer;

    &--error {
      display: none;
    }

    @include vp-1023 {
      width: 20px;
      height: 20px;
      top: 20px;
      right: 25px;
    }
  }

  &__button:hover {
    opacity: 0.8;
  }

  &__button:active {
    opacity: 1;
  }

  &__error-text {
    display: none;
    text-align: center;
    font-weight: 500;
    line-height: 22px;
    letter-spacing: -0.3px;
    color: $color-international-orange;
  }

  &.is-error {
    .search-form__input {
      color: $color-international-orange;
    }

    .search-form__button--search,
    .search-form__label {
      display: none;
    }

    .search-form__button--error {
      display: flex;
    }

    .search-form__error-text {
      display: block;
    }
  }
}
