.btn {
  position: relative;
  display: inline-flex;
  justify-content: center;
  padding: 15px 30px;
  user-select: none;

  border: none;
  border-radius: 12px;
  cursor: pointer;
  font-size: 18px;
  line-height: 1.12em;
  font-weight: 700;

  transition: opacity $trans-default;

  &--red {
    background-color: $color-torch-red;
  }

  &:disabled {
    opacity: 0.5;

    pointer-events: none;
  }

  @include hover-focus {
    opacity: 0.8;
  }

  span {
    z-index: 2;
  }
}

.btn--primary {
  color: $color-dodger-blue;

  background-color: $color-hawkes-blue;

  .btn__progress-bar {
    content: "";
    display: block;
    background: linear-gradient(90deg, rgba(122, 197, 255, 1) 0%, rgba(91, 128, 255, 1) 100%);
    position: absolute;
    transition: 0.3s;
    width: 0;
    height: 100%;
    top: 0;
    left: 0;
    border-radius: 12px 0 0 12px;
  }
}

.btn--primary:hover {
  color: $color-default-white;

  .btn__progress-bar {
    width: 90%;
  }
}

.btn--primary:active {
  .btn__progress-bar {
    width: 100%;
    border-radius: 12px;
    background: linear-gradient(90deg, rgba(53, 159, 241, 1) 0%, rgba(54, 61, 232, 1) 100%);
  }
}

.btn--primary:disabled {
  background-color: $color-snow;
  color: $color-heather;
  opacity: 1;
}

.btn--secondary-blue {
  color: $color-default-white;
  background-color: $color-dodger-blue;
}

.btn--secondary-blue:hover {
  background-color: $color-summer-sky;
}

.btn--secondary-blue:active {
  background-color: $color-navy-blue;
}

.btn--secondary-grey {
  color: $color-dodger-blue;
  background-color: $color-solitude;
}

.btn--secondary-grey:hover {
  background-color: $color-alice-blue;
}

.btn--secondary-grey:active {
  background-color: $color-dodger-blue;
  color: $color-default-white;
}

.btn--tertiary {
  color: $color-picton-blue-second;
  background-color: $color-alice-blue-second;
  overflow: hidden;

  @include vp-1023 {
    color: $color-default-white;
  }

  .btn__progress-bar {
    content: "";
    display: block;
    background: linear-gradient(270deg, #e7f6ff 7.47%, #17c7ff 7.47%, #7ae7ff 95.02%);
    position: absolute;
    transition: 0.3s;
    width: 0;
    height: 100%;
    top: 0;
    left: 0;

    @include vp-1023 {
      background: linear-gradient(270deg, #00baf5 1.81%, #5ae1ff 95.02%);
      width: 90%;
    }
  }
}

.btn--tertiary:hover {
  color: $color-alice-blue-second;

  @include vp-1023 {
    color: $color-default-white;
  }

  .btn__progress-bar {
    width: 90%;
  }
}

.btn--tertiary:active {
  color: $color-default-white;

  .btn__progress-bar {
    width: 100%;
    background: linear-gradient(270deg, #00baf5 1.81%, #5ae1ff 95.02%);
  }
}

.btn--tertiary:disabled {
  background-color: #eff0f0;
  color: #bac1c7;

  .btn__progress-bar {
    width: 0;
  }
}
