.personal {
  display: grid;
  grid-template-columns: 1fr 1fr;
  column-gap: 30px;
  width: 100%;
  max-width: 1440px;
  margin-top: 117px;
  margin-bottom: 39px;
  margin-left: auto;
  margin-right: auto;
  padding: 0 162px 0 168px;
  color: $color-nero;

  @include vp-1439 {
    padding: 0 clamp(115px, $padding-desktop, 165px);
  }

  @include vp-1199 {
    padding: 0 clamp(100px, $padding-desktop-small, 120px);
  }

  @include vp-1023 {
    display: flex;
    flex-direction: column;
    margin: 0 0 20px;
    padding: 0;
  }

  &__wrapper {
    display: grid;
    grid-template-columns: 1fr;
    row-gap: 30px;

    @include vp-1023 {
      padding: 0 clamp(32px, $padding-tablet, 115px);
      row-gap: 32px;
    }

    @include vp-767 {
      padding: 0 clamp(20px, $padding-mobile, 32px);
    }
  }

  &__level-info {
    grid-row: 1 / 2;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 0 32px 32px;
    background: linear-gradient(160deg, rgba(23, 199, 255, 1) 4%, rgba(102, 227, 255, 1) 22%, rgba(164, 239, 255, 1) 35%, rgba(255, 255, 255, 1) 65%);
    border-radius: 40px;

    @include vp-1023 {
      border-radius: 0;
      padding: 0 clamp(32px, $padding-tablet, 115px);
      padding-top: 48px;
    }

    @include vp-767 {
      padding: 0 clamp(20px, $padding-mobile, 32px);
      padding-top: 48px;
    }
  }

  &__image {
    @include vp-1023 {
      display: none;
    }
  }

  &__image-mobile {
    display: none;

    @include vp-1023 {
      display: block;
    }
  }

  &__title {
    margin: 0 0 16px;
    font-size: 44px;
    line-height: 44px;
    letter-spacing: -0.8px;

    @include vp-1023 {
      margin-top: -6px;
      margin-bottom: 12px;
      font-size: 40px;
      line-height: 40px;
      letter-spacing: -0.3px;
    }
  }

  &__subtitle {
    margin: 0 0 8px -2px;
    line-height: 38px;
    letter-spacing: -0.6px;

    @include vp-1023 {
      margin-left: 0;
      line-height: 29px;
      letter-spacing: -0.5px;
    }
  }

  &__days {
    display: flex;
    justify-content: center;
    font-weight: 700;
    font-size: 18px;
    line-height: 20px;
    letter-spacing: -0.2px;
    min-width: 192px;
    min-height: 50px;
    margin-bottom: 23px;
    padding: 15px 30px;
    background-color: $color-alice-blue-second;
    color: $color-picton-blue-second;
    border-radius: 12px;

    @include vp-1023 {
      margin-bottom: 12px;
    }
  }

  &__text {
    text-align: center;
    font-weight: 500;
    line-height: 22px;
    letter-spacing: -0.3px;
    margin: 0 0 23px;

    @include vp-1023 {
      margin: 0 5px 32px;
      text-align: start;
    }
  }

  &__link {
    margin-bottom: 5px;
    min-width: 325px;
    line-height: 24px;
    letter-spacing: -0.4px;

    @include vp-1023 {
      display: none;
    }
  }

  &__countdown {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: self-start;
    justify-content: center;
    min-height: 258px;
    padding: 32px 32px 32px 104px;
    background-color: $color-default-white;
    border-radius: 40px;

    @include vp-1023 {
      min-height: 52px;
      padding: 15px 15px 15px 50px;
      order: 1;
      background-color: $color-alice-blue-second;
      border-radius: 12px;
      color: $color-default-black;
    }
  }

  &__countdown-icon {
    position: absolute;
    top: 107px;
    left: 32px;

    @include vp-1023 {
      display: block;
      width: 20px;
      height: 22px;
      top: 15px;
      left: 15px;
    }
  }

  &__countdown-text {
    margin: 0;
    font-weight: 500;
    font-size: 24px;
    line-height: 29px;
    letter-spacing: -0.5px;

    @include vp-1023 {
      font-size: 16px;
      line-height: 21px;
      letter-spacing: -0.3px;
    }
  }

  &__countdown-time {
    margin: 0;
    font-weight: 700;
    font-size: 32px;
    line-height: 38px;
    letter-spacing: -0.6px;

    @include vp-1023 {
      font-size: 16px;
      line-height: 20px;
      letter-spacing: -0.3px;
    }
  }

  &__want-to-win {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 32px 34px;
    background-color: $color-default-white;
    border-radius: 40px;

    @include vp-1023 {
      align-items: start;
      padding: 18px;
      border: 2px solid $color-viking;
      border-radius: 12px;
    }

    p {
      margin: 0 0 20px;
      font-weight: 500;
      font-size: 18px;
      line-height: 24px;
      letter-spacing: -0.5px;

      @include vp-1023 {
        font-size: 16px;
        line-height: 22px;
        letter-spacing: -0.3px;
      }
    }
  }

  &__scale {
    width: 100%;
    height: 24px;
    margin-bottom: 20px;
    background-color: $color-alice-blue-second;
    border-radius: 20px;

    @include vp-1023 {
      height: 12px;
    }
  }

  &__progress-bar {
    width: 22.8%;
    height: 100%;
    background: linear-gradient(270deg, #17c7ff 0%, #81e8ff 100%);
    border-radius: 20px;
  }

  &__button {
    margin-bottom: 14px;
    max-width: 189px;
    width: 100%;

    @include vp-1023 {
      align-self: center;
    }

    .btn-text {
      line-height: 20px;
      letter-spacing: -0.4px;
    }
  }

  &__info {
    color: $color-nobel;
    font-weight: 500;
    line-height: 22px;
    letter-spacing: -0.3px;
  }
}
