.monthly-draw {
  margin-top: 300px;

  .prize-category {
    @include vp-1199 {
      padding: 21px;
    }

    @include vp-1023 {
      width: 100%;
    }
  }

  .prize-category__title {
    margin-bottom: 23px;
  }

  .category-prize-list {
    @include vp-1199 {
      gap: 21px;
    }

    @include vp-1023 {
      gap: 32px;
    }
  }

  .agreement {
    .checkbox {
      margin-bottom: 13px;
    }
  }
}

.monthly-draw__wrapper {
  padding-bottom: 32px;
}

.monthly-draw__title {
  margin: 0;
  margin-bottom: 27px;
}

.monthly-draw__categories {
  display: flex;
  gap: 38px;
  padding-bottom: 50px;

  @include vp-767 {
    flex-direction: column;
    gap: 0;
  }
}

.monthly-draw__agreement {
  display: flex;
  justify-content: center;

  .agreement__button {
    min-width: 327px;
  }
}
