.banner-winners {
  @include vp-1023 {
    padding-top: 18.5%;
    overflow: hidden;
  }

  .container {
    padding-bottom: 50px;

    @include vp-1023 {
      padding: 0;
    }
  }

  &__wrapper {
    position: relative;
    padding: 18px 94px 18px 40px;
    min-height: 316px;

    display: grid;
    grid-template-columns: auto auto;
    border-radius: 40px;
    background-color: $color-default-white;

    @include vp-1439 {
      padding: 18px 40px;
    }

    @include vp-1023 {
      min-height: unset;
      padding: 0;
      display: flex;
      flex-direction: column;
      justify-content: center;
    }
  }

  &__text-wrapper {
    position: relative;
    z-index: 3;

    @include vp-1023 {
      display: flex;
      flex-direction: column;
      align-items: center;
      padding: 50px 0 0;
    }
  }

  &__header {
    margin-top: 43px;
    margin-bottom: 40px;
    line-height: 1em;

    @include vp-1023 {
      color: $color-default-white;
      letter-spacing: -0.3px;

      margin: 0;
      margin-bottom: 80px;
    }

    @include vp-767 {
      font-size: 30px;
      margin-bottom: 50px;
    }

    @include vp-499 {
      font-size: 24px;
      margin-bottom: 22px;
    }

    @include vp-374 {
      font-size: 21px;
    }

    br {
      @include vp-1023 {
        display: none;
      }
    }
  }

  &__link {
    width: 327px;

    @include vp-1023 {
      background-color: $color-default-white;
      margin-bottom: 52px;
    }

    @include vp-374 {
      width: 280px;
    }
  }

  &__images-wrapper {
    @include vp-1439 {
      display: flex;
      align-items: center;
      justify-content: flex-end;
    }
  }

  &__image-wrapper {
    position: relative;
    z-index: 3;

    img {
      margin-left: auto;
    }
  }

  &__image-wrapper--background {
    position: absolute;
    z-index: 2;
    top: 0;
    bottom: 0;
    right: 0;

    @include vp-1023 {
      position: relative;
      width: 100%;
      z-index: 0;
    }

    img {
      border-radius: 0 40px 40px 0;
      height: 100%;

      @include vp-1023 {
        width: 100%;
        border-radius: unset;
      }
    }
  }

  &__image-wrapper--background-desktop {
    @include vp-1023 {
      display: none;
    }
  }

  &__image-wrapper--background-mobile {
    display: none;

    @include vp-1023 {
      display: block;
      position: absolute;
      bottom: -60px;
    }
  }

  &__image-wrapper--decorative {
    @include vp-1023 {
      position: absolute;
      width: 23.8%;
      left: 50%;
      top: 0;
      z-index: 2;
      transform: translate(-50%, -68%);
    }
  }

  &__image-wrapper--decorative-desktop {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    height: 100%;

    @include vp-1023 {
      display: none;
    }
  }

  &__image-wrapper--decorative-mobile {
    display: none;

    @include vp-1023 {
      display: block;
    }
  }
}
