.card-prize {
  position: relative;
  max-width: 156px;
  height: 100%;
  min-height: 204px;
  padding: 10px 6px 0 10px;
  border-radius: 12px;

  &--wide {
    @include vp-1023 {
      width: initial;
      min-width: 156px;
    }
  }

  &--default {
    background-color: $color-default-white;
  }

  &--mint {
    background: $mint-tulip-to-down;
  }

  &--green {
    background: $conifer-to-down;
  }

  &--bonus-green {
    background: $color-hint-of-green;
  }

  &--pink {
    background: $classic-rose-to-down;
  }

  &--bonus-pink {
    background-color: $color-rose-white;
  }

  &--pale {
    background: $pale-turquoise-to-down;
  }

  &--fog {
    background: $fog-to-down;
  }

  &--blue {
    background: $columbia-blue-to-down;
  }

  &--bonus-blue {
    background-color: $color-pattens-blue;
  }

  &--bonus-blue-light {
    background-color: $color-alice-blue;
  }

  &--cian {
    background-color: $color-light-cian;
  }

  &--light-yellow {
    background-color: $color-light-yellow;
  }

  &--snow-flurry {
    background-color: $color-snow-flurry;
  }

  &--lavender-blue {
    background: $lavender-blue-to-down;
  }

  &--pale-slate {
    background: $pale-slate-to-down;
  }

  &--dairy-cream {
    background: $color-dairy-cream-to-down;
  }

  &--quartz {
    background: $color-quartz-to-down;
  }

  &--almond {
    background: $color-almond-to-down;
  }

  &__logo-wrapper {
    display: flex;
    gap: 9px;
    flex-wrap: wrap;
    margin-bottom: 5px;

    img {
      position: relative;

      @include vp-374 {
        max-width: 80px;
      }
    }
  }

  &__partners-wrapper {
    display: flex;
    gap: 4px;
    flex-wrap: wrap;
  }

  &__logo-text {
    font-weight: 700;
    letter-spacing: -0.4px;

    &--default {
      color: $color-nero;
    }

    &--pale {
      color: $color-pelorous;
    }

    &--green {
      color: $color-limeade;
    }

    &--blue {
      color: $color-picton-blue;
    }
  }

  &__info-icon {
    display: block;
  }

  &__title {
    position: relative;
    z-index: 1;
    margin: 0 14px 5px 0;
    line-height: 124%;
    letter-spacing: -0.4px;
  }

  &__text {
    position: relative;
    z-index: 1;
    display: block;
    font-size: 12px;
    line-height: 18px;
  }

  &__image-wrapper {
    position: absolute;
    width: 100%;
    bottom: 0;
    right: 0;
  }

  &__image-wrapper img {
    margin-left: auto;
  }
}
