.just-participate {
  padding-top: 60px;
  padding-bottom: 140px;

  @include vp-1023 {
    padding-top: 0;
    padding-bottom: 9px;
  }

  &__container {
    position: relative;
    max-width: 1440px;
    min-height: 400px;
    padding: 81px 40px 80px;
    display: grid;
    background-color: $color-default-white;
    border-radius: 40px;
    color: $color-nero;

    @include vp-1023 {
      min-height: 0;
      padding: 0;
      background-color: transparent;
    }
  }

  &__image-box {
    position: absolute;
    top: -79px;
    right: calc(50% - 29px);
    left: -50px;

    @include vp-1023 {
      display: none;
    }
  }

  &__wrapper {
    max-width: calc(50% - 15px);
    width: 100%;
    margin-left: auto;

    @include vp-1023 {
      max-width: 100%;
      margin: 0;
    }
  }

  &__title {
    margin: 0 0 20px;
    letter-spacing: -0.8px;
    line-height: 48px;

    @include vp-1023 {
      margin-bottom: 20px;
      letter-spacing: -0.7px;
      font-size: 32px;
      line-height: 38px;
    }
  }

  &__text {
    margin: 0 0 10px;
    font-weight: 500;
    font-size: 24px;
    line-height: 32px;
    letter-spacing: -0.3px;

    @include vp-1023 {
      font-size: 16px;
      line-height: 22px;
    }
  }
}
