.bonuses {
  padding: 32px 32px 40px;
  background-color: $color-default-white;
  border-radius: 40px;

  @include vp-1023 {
    padding: 0;
  }

  &__title {
    margin: 0 0 8px;
  }

  &__text {
    margin: 0 0 24px;
    font-weight: 500;
  }

  &__category-prize-list {
    gap: 16px;

    @include vp-1023 {
      gap: 0;
    }
  }

  .card-prize {
    width: 160px;

    @include vp-1023 {
      width: 172px;
    }

    &--wide {
      width: 336px;

      @include vp-1023 {
        width: 172px;
      }
    }
  }

  .card-prize__title {
    font-size: 18px;
    line-height: 110%;
  }

  .card-prize__text {
    font-size: 14px;
    line-height: 18px;
    font-weight: 500;
  }

  &__swiper.swiper {
    width: calc(100% + 24px);
  }
}
